import React from "react";
import { useKeenSlider } from "keen-slider/react";
import "keen-slider/keen-slider.min.css";
import "./Gallery.module.scss";

import image2 from 'assets/images/배경이미지.jpg';
import bkImgIn from 'assets/images/사진_1.jpg';
import bkImgOut from 'assets/images/사진_2.jpg';

const images = [
  { id: 1, src: image2, alt: "Image 1" },
  { id: 2, src: bkImgIn, alt: "Image 2" },
  { id: 3, src: bkImgOut, alt: "Image 3" },
  // { id: 4, src: "/images/배경이미지.jpg", alt: "Image 4" },
];

const Gallery = () => {
  const [sliderRef] = useKeenSlider({
    loop: true,
    mode: "snap",
    slides: { perView: 1, spacing: 0 },
  });

  return (
    <div className="gallery">
      <h2>Image Gallery</h2>
      
      <div ref={sliderRef} className="keen-slider">
        {images.map((image) => (
          <div key={image.id} className="keen-slider__slide">
            <img src={image.src} alt={image.alt} />
          </div>
        ))}
      </div>
    </div>
  );
};

export default Gallery;